
<!--
    가입하기는 기본적인 폼만 제공됩니다
    기능명세에 따라 개발을 진행하세요.
    Sub PJT I에서는 UX, 디자인 등을 포함하여 백엔드를 제외하여 개발합니다.
 -->
<template>
    <div class="user" id="join">
         <div class="wrapC">

        <h1>가입하기</h1>
        <div >
            <div class="input-with-label">
                <input v-model="nickName" 
                       @keyup.enter="join"
                       id="nickname"
                       placeholder="닉네임을 입력하세요." type="text"/>
                <label for="nickname">닉네임</label>
            </div>

            <div class="input-with-label">
                <input v-model="email" v-bind:class="{error : error.email, complete:!error.email&&email.length!==0}"
                       @keyup.enter="join"
                       id="email" placeholder="이메일을 입력하세요."
                       type="text"/>
                <label for="email">이메일</label>
                <div class="error-text" v-if="error.email">
                    {{error.email}}
                </div>

            </div>

            <div class="input-with-label">
                <input v-model="password"
                        @keyup.enter="join"
                       id="password" :type="passwordType"
                       placeholder="비밀번호를 입력하세요."/>
                <label for="password">비밀번호</label>
                <div class="error-text" v-if="error.password">
                    {{error.password}}

                </div>
            </div>

            <div class="input-with-label">
                <input v-model="passwordConfirm" :type="passwordConfirmType"
                        id="password-confirm"
                       placeholder="비밀번호를 다시한번 입력하세요."/>
                <label for="password-confirm">비밀번호 확인</label>
                <div class="error-text" v-if="error.password">
                    {{error.passwordConfirm}}

                </div>
            </div>
        </div>

        <label >
            <input v-model="isTerm" type="checkbox" id="term"/>
            <span>약관을 동의합니다.</span>
        </label>

        <span @click="termPopup=true">약관보기</span>

        <button class="btn btn--back btn--join" v-on:click="join" :disabled="!isSubmit"
                :class="{disabled : !isSubmit}">
            가입하기
        </button>

        </div>

    </div>
</template>

<script>
    import '../../assets/css/style.scss'
    import '../../assets/css/user.scss'
    import * as PasswordValidator from 'password-validator'
    import * as EmailValidator from 'email-validator';
    export default {
        created(){
            // data를 불러와야함
        },
        watch: {
            nickName: function (v) {
                this.checknickName();
            },
            email: function (v) {
                this.checkEmail();
            },
            password: function (v) {
                this.checkPassword();
            },
            passwordConfirm: function (v) {
                this.checkConfirm();
            },
        },
        methods: {
            checknickName(){
                if(this.nickName.length == 0) this.error.nickName = "닉네임을 입력해주세요"
                else this.error.nickName = false;
            },
            checkEmail(){
                if (this.email.length >= 0 && !EmailValidator.validate(this.email))
                    this.error.email = "이메일 형식이 아닙니다."
                else this.error.email = false;

                let isSubmit = true;
                Object.values(this.error).map(v => {
                    if (v) isSubmit = false;
                })
                this.isSubmit = isSubmit;


            },
            /// 비밀번호 형식체크 아직 안됨 ....
            checkPassword(){
                if (this.password.length >= 0 && !EmailValidator.validate(this.password))
                    this.error.password = '영문,숫자 포함 8 자리이상이어야 합니다.'
                else
                    this.error.password = false;

            },
            checkConfirm(){
                if(this.error.password == false){
                    
                    if(this.passwordConfirm.length >=0 && this.password != this.passwordConfirm){
                        this.error.passwordConfirm = "비밀번호가 일치하지 않습니다."
                    }
                    else
                        this.error.passwordConfirm = false;
                }
            },
            join(){
                if(this.isSubmit){
                    //
                }
            }
        },
        data: () => {
            return {
                email: '',
                password: '',
                passwordConfirm: '',
                nickName: '',
                isTerm: false, // 약관동의
                isLoading: false,
                error: {
                    email: false,
                    password: false,
                    nickName: false,
                    passwordConfirm: false,
                    term: false
                },
                isSubmit: false,
                passwordType: "password",
                passwordConfirmType: "password",
                termPopup: false,
            }
        }

    }

</script>


